function Heading({scrollToGame,mobile}) {
    return  <section >

        {
            /*

                {mobile?<MobileView/>:<PcView/> }

                {!mobile && <div style={{
                    display: "flex",
                    alignItems: "center",
                    justifyContent: "center",

                // ...(mobile?{}:{
                    position: "absolute",
                    top: mobile?"70vh":"85vh",
                    left: 0,
                    right: 0,
                // }),
                }}>
                    <ArrowButton onClick={scrollToGame} purple={mobile} small={false}/>
                </div>}
            */
        }
        <Generic />
    </section>
}

const Generic = () => {
    return (
        <div className="yoozmob-header d-none">
            <div className="offers-bg flex-start-center">
                <div className="banner-container">
                    <div className="div1">
                        <img src="../images/2023/illimite.png" alt=""/>
                    </div>
                    <div className="div2">
                        <img src="../images/2023/yooz_by_ooredoo.png" alt=""/>
                    </div>
                    <div className="div3">
                        <img src="../images/2023/roogi.png" alt=""/>
                    </div>
                    <div className="div4 ">
                        <div className="app-links">
                            <div className="download-container no-bottom-margin">
                                <a href="https://play.google.com/store/apps/details?id=dz.ooredoo.ibiza" target="_blank"
                                   className="btn-download">
                                    <img src="../images/2023/google_play.png"/>
                                </a>
                                <a href="https://apps.apple.com/dz/app/yooz/id1558857791?l=fr"
                                   target="_blank"
                                   className="btn-download">
                                    <img src="../images/2023/app_store.png"/>
                                </a>
                                <a href="https://appgallery.huawei.com/#/app/C103964575?sharePrepath=ag&amp;%3Blocale=fr_FR&amp;%3Bsource=appshare&amp;%3Bsubsource=C103964575"
                                   target="_blank"
                                   className="btn-download last">
                                    <img src="../images/2023/app_gallery.png"/>
                                </a>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div className="head-faded">

            </div>
        </div>
    )
}
