import React from "react";
import doubleArrow from "../Assets/double-arrow.png"
//import './ArrowButton.scss';

function ArrowButton({onClick,purple=true,rotate=true,small=true,className='',style={}}) {
    return <button  className={`arrow-button-heading ${purple?'purple':''} ${small?'small':''} ${className}`} style={style}>
        <img src={doubleArrow} alt="" style={{height:'100%',width:'100%' ,transform:`rotate(${rotate?0:180}deg)`}}/>
    </button>
}

export default ArrowButton;
