import React, {useState} from "react";
import {SvgText} from "../ScrollGame/Game/Game";

//import './IconText.scss';

function IconText({setAmount,offer,amount,text,icon,clickable=true,width='50%',scale=1,iconOnly, style, innerStyle }) {
    const [isSelected, set_isSelected] = useState(false);
    const selectIcon = () => {
        if(clickable){
            set_isSelected(!isSelected);
            setAmount(amount+(isSelected?-offer:offer))
        }
    };
    return  ((text||iconOnly) ?<div
        onClick={selectIcon}
        style={{
            height:'4em',
            width:`calc(${width} - 4px)`,
            margin:'1px',
            pointerEvents: 'bounding-box',
            ...(iconOnly?{display: 'flex',
            alignItems: 'center'}:{}),
            ...(isSelected ? {backgroundColor:'rgba(99 ,0 ,213 , 0.2)', borderRadius: '0.5em'} : {}),
            // ...(isSelected ? {backgroundColor:'rgba(151,154,175,0.36)', borderRadius: '0.5em'} : {})
            // backgroundColor:'#00ff00'
            ...style,
        }}>
        <div style={{height:'45%',paddingTop:'5%',width:'100%',pointerEvents: 'none', ...innerStyle }} className="sp-icon flex-center">
            <img src={icon} alt="" style={{height:'100%',pointerEvents: 'none',transform:`scale(${scale})`}}/>
        </div>
        {text && <div style={{height:'50%',width:'100%',pointerEvents: 'none',}} className="sp-text flex-center">
            <SvgText viewBox={"0 0 150 30"}
                     style={{fill: 'black', transform: 'scale(3) translateY(0%)',pointerEvents: 'none',}}
                     textStyle={{fontFamily: 'BebasNeue'}}>{text}</SvgText>
        </div>}
    </div>:<></>)
}

export default IconText;
