import React, {useRef, useState} from "react";
import RoogyHand from '../../../Assets/ROOGY_hand_phone.png'
import './Phone.css';
import Game, {Tabs} from "./Game";
import ScrollGameHeading from "../ScrollGameHeading";
import ArrowButton from "../../ArrowButton";

function Phone({mobile,selectedTab, set_selectedTab}) {
    const getFog = () => {
        switch (selectedTab) {
            default :return 5;
            case 1:return 10;
            case 2:return 10;
        }
    };
    const container=useRef(null);
    return <div className={`phone ${mobile?'mobile':''}`}>
        {!mobile && <div className={`game-ui-container ${mobile ? 'mobile' : ''}`}>
            <Tabs set_selectedTab={set_selectedTab} selectedTab={selectedTab}/>
        </div>}
        <div >
            <Container mobile={mobile} _ref={container} map={selectedTab}>

                {mobile && <div className={`game-ui-container ${mobile ? 'mobile' : ''}`}
                style={{background: `linear-gradient(180deg, rgba(255,255,255,1) 0%, rgba(99,0,213,0) ${getFog()}%, rgba(159,99,229,0) ${100-getFog()}%, rgba(255,255,255,1) 100%)`}}
                >
                     <div className="scroll-game">
                         <div style={{
                             display: "flex",
                             alignItems: "center",
                             justifyContent: "center",

                             // ...(mobile?{}:{
                             position: "absolute",
                             top: '35vh',
                             left: 0,
                             right: 0,
                             // }),
                         }}>
                             <ArrowButton onClick={()=>{}} purple={mobile} small={false}/>
                         </div>
                        <ScrollGameHeading/>
                    </div>

                </div>}
                <Game mobile={mobile} container={container} selectedTab={selectedTab}/>
            </Container>
        </div>

        {!mobile && <img src={RoogyHand} alt="" style={{
            position:'absolute',
            pointerEvents: 'none',
            height:'100%',
            zIndex: 150,
            transform:`scale(${1.55})`
        }}/>}
    </div>
}

const Container = ({mobile,children,_ref,map}) => {
    const style={
        ...(mobile?{
            width:'100vw',
            // height:map===1?'300vh':'100vh',
            position:'relative',
            overflowY: mobile?'hidden':'scroll',
            overflowX: 'hidden',
            display: 'flex',
            flexDirection: 'column-reverse',
        }:{
            width:'38vh',
            height:'76vh',
            position:'relative',
            overflowY: 'scroll',
            overflowX: 'hidden',
            display: 'flex',
            flexDirection: 'column',
        })
    };
    return <div style={style} className={"phone-container"} ref={_ref}>
        {mobile &&  <CoverFix top={true}/>}
        {children}
        {mobile &&  <CoverFix/>}
    </div>
};
const CoverFix = ({top=false}) => {
    // this shit is demanded by the client because it has a shit screen to cover default render glitch on her pc
    return <div style={{position:'absolute', height:"4px" , width:'100%',backgroundColor:"white",...(top?{top:'-2px'}:{bottom:'-2px'}) }}/>
};
export default Phone;
