import React, {useEffect, useRef, useState} from "react";
import Heading from "./Components/Heading/Heading";
import Footer from "./Components/Footer/Footer";
import ScrollGame from "./Components/ScrollGame/ScrollGame";
import useWindowDimensions from "./Components/useWindowDimensions";

function App() {
    const { height, width } = useWindowDimensions();
    const [mobile, set_mobile] = useState(false);
    useEffect(() => {
        set_mobile( width<768 || (width/height)<=1 );
    }, [height,width]);

    const [appAcroll, set_appAcroll] = useState("");
    useEffect(() => {
        set_appAcroll("")
    }, []);
    const scrollToGame = () => {
        set_appAcroll(`app-scroll-to-game`)
    };
    const scrollToHeading = () => {
        set_appAcroll(`app-scroll-to-heading`)
    };
    const scrollToFooter = () => {
        set_appAcroll(`app-scroll-to-footer`)
    };

    const [transform, set_transform] = useState(0);
    const handleScroll=(event) =>{
        let scrollTop = event.deltaY;
            // itemTranslate = Math.min(0, scrollTop/3 - 60);
        set_transform(scrollTop);
        console.log(event);
    };

    // useEffect(() => {
    //     window.addEventListener('wheel',handleScroll);
    //     return () => {
    //         window.removeEventListener("wheel", handleScroll);
    //     };
    // }, []);




    return (
        <div className={appAcroll}>
            <Heading scrollToGame={scrollToGame} mobile={mobile}/>
            <ScrollGame scrollToHeading={scrollToHeading} scrollToFooter={scrollToFooter} mobile={mobile}/>
            <Footer scrollToGame={scrollToGame} mobile={mobile}/>
        </div>
    );
}

export default App;
