import React from "react";
import IconText from "./IconText";
import Phone from "../../Assets/map/social/minutes.svg";
import Sms from "../../Assets/map/social/sms.svg";
import SmsPhone from "../../Assets/map/social/smsphone.svg";
import PubgJackpot2 from "../../Assets/map/social/pubg-jackpot2.svg";
import Data from "../../Assets/map/social/data.svg";
import { Textfit } from 'react-textfit';
import {SvgText} from "../ScrollGame/Game/Game";
import FB_MESS from "../../Assets/map/social/fb_mesenger.svg";
//import './Bundle.scss';

function Bundle({item}) {
    return <div  style={{
        height:'100%',
        width:'100%',
    }}>
        {(item.appels||item.ooredoo) && <div style={{width:'100%',height:'5em',borderBottom: '1px dashed'}} className="flex-center">
   
            { 
                item.appels ?
                <div style={{ display: 'flex', flexDirection: 'row', width: '50%' }}>
                    <IconText icon={Phone} text={item.appels} clickable={false} width={'100%'} iconOnly={!item.appels}/>
                </div>
                : null
            }
            {item.ooredoo && <div style={{width:'60%',height:'100%',textAlign: 'center',fontFamily: 'BebasNeue'}} className="flex-center-column">
                
                {
                    item.ooredoo?.includes('SMS') || item.ooredoo?.includes('min') ?
                    <IconText 
                        icon={
                            item.ooredoo?.includes('min') ? Phone :
                            item.ooredoo?.includes('SMS ') ? SmsPhone : Sms} 
                        clickable={false} width={'100%'} iconOnly={true} style={{ marginTop: -20, marginBottom: -10 }} />
                    : null
                }
                <SvgText viewBox={"0 0 150 30"}
                         style={{fill: 'black', transform: 'scale(2) translateY(0%)',pointerEvents: 'none',}}
                         textStyle={{fontFamily: 'BebasNeue'}}>{item.ooredoo.includes('SMS ') ? `${item.ooredoo.replaceAll('SMS ', '')}` : item.ooredoo}</SvgText>
                <SvgText viewBox={"0 0 150 30"}
                         style={{fill: 'black', transform: 'scale(2) translateY(0%)',pointerEvents: 'none',}}
                         textStyle={{fontFamily: 'BebasNeue'}}>vers ooredoo</SvgText>
            </div>}

        </div>}
        <div style={{width:'100%',height:'4em',flexWrap: 'wrap',}}  className="flex-center">
            {
                item.bonus2Gb ?
                <IconText
                    icon={PubgJackpot2}
                    style={{ height: 80 }}
                    clickable={false} iconOnly={true} />
                //     <IconText icon={Data} text={"2GB"} clickable={false}  />
                : null
            }
            <IconText icon={Data} text={item.data} clickable={false}  />
            <IconText icon={FB_MESS} text={item.fb_messenger} clickable={false}  />
        </div>
        {item.bonus && <div style={{width:'100%',height:'3em',}}  className="flex-center">
            <SvgText viewBox={"0 0 150 30"}
                     style={{fill: 'red', transform: 'scale(1.6) translateY(0%)',pointerEvents: 'none',}}
                     textStyle={{fontFamily: 'BebasNeue'}}>+ {item.bonus}</SvgText>
        </div>}
    </div>
}

export default Bundle;
