import React, {useEffect, useState} from "react";
import Facebook from "../../Assets/map/social/facebook.svg"
import Messenger from "../../Assets/map/social/messenger.svg"
import Instagram from "../../Assets/map/social/instagram.svg"
import Whatsapp from "../../Assets/map/social/whatsapp.svg"
import Youtube from "../../Assets/map/social/youtube.svg"
import Snapchat from "../../Assets/map/social/snapchat.svg"
import Midnight from "../../Assets/map/social/Midnight.svg"
import MobileData from "../../Assets/map/social/data.svg"
import Phone from "../../Assets/map/social/minutes.svg"

import mpButton from "../../Assets/map/button.png"
import RightTop from "../../Assets/map/Bubble/right_top.svg"
import RightBot from "../../Assets/map/Bubble/right_bot.svg"
import LeftTop from "../../Assets/map/Bubble/left_top.svg"
import LeftBot from "../../Assets/map/Bubble/left_bot.svg"
import bgs from "./svgCss"
import './BubbleX.css';
import {SvgText} from "../ScrollGame/Game/Game";
import SuperPower from "./SuperPower";
import Bundle from "./Bundle";

function BubbleX({x = 0, y = 0, item, pack = {}, validity = '',show=false}) {
    const [selectedTab, set_selectedTab] = useState(0);
    const [total, set_total] = useState(item.bundle);
    useEffect(() => {
        set_total(item.bundle);
        console.log(item,validity);
    }, [selectedTab]);
    return <div style={{
        display:show?'block':'none',
        position: 'absolute',
        width: '300%',
        bottom: '50%',
        left: `${x<0?-278:90}%`,
        // transform: `translate(${x}%, ${y}%) scale(0.9)`,
        transform: `scale(0.85)`,
    }}>
        <BubbleHeader x={x} item={item} selectedTab={selectedTab} set_selectedTab={set_selectedTab}/>
        <BubbleContainer x={x}>
            {selectedTab===0 && <Bundle item={item}/>}
            {selectedTab===1 && <SuperPower pack={pack} set_total={set_total} total={total} item={item} validity={validity}/>}
        </BubbleContainer>
        <BubbleFooter x={x} selectedTab={selectedTab} validity={validity} total={total} pack={pack}/>
    </div>
}

const BubbleHeader = ({x,set_selectedTab,selectedTab,item}) => {
    return <div style={{width:'100%',transform:`translateY(${4}px)`,pointerEvents: 'none',}}>
        <div style={{
            position:'absolute',
            height:'50%',
            width: '100%', textTransform: 'uppercase', pointerEvents: 'none',
            display: 'flex',
            borderBottom: '1px solid rgba(0,0,0,0.3)',
            justifyContent: 'center',
            // backgroundColor:'#00ff00'
        }}>
            <SvgText
                style={{fill: 'black', transform: 'scale(4) translateY(3%)'}}
                textStyle={{fontFamily: 'BebasNeue'}}>{item.bundle} DA</SvgText>

        </div>

        <div style={{position:'absolute',width: '95%',left: x<0?'3.5%':0,margin: '0 1%', height: '50%', display: 'flex', alignItems: 'center', justifyContent: 'center',backgroundColor:'#ececec', bottom:0}}>
            <div style={{position:'absolute',height:'100%',width:selectedTab === 1 ?'58%':'42%',backgroundColor:'white',left:selectedTab === 1 ?'42%':'0%',pointerEvents: 'none'}}>

            </div>
            <div style={{position:'absolute',height:'100%',width:selectedTab === 1 ?'42%':'58%',left:selectedTab === 1 ?'0%':'42%',pointerEvents: 'visible'}} onClick={() => set_selectedTab(selectedTab===0?1:0)}>

            </div>
            <SvgText
                     style={{fill: selectedTab === 0 ? '#000000' : '#00000063', transform: 'scale(1.2)',pointerEvents: 'none'}}
                     textStyle={{fontFamily: 'BebasNeueRegular',pointerEvents: 'none',letterSpacing: '1px'}}>Jackpot</SvgText>
            <SvgText
                     style={{fill: selectedTab === 1 ? '#000000' : '#00000063', transform: 'scale(1.2) translateX(-6%)',pointerEvents: 'none'}}
                     textStyle={{fontFamily: 'BebasNeueRegular',pointerEvents: 'none',letterSpacing: '1px'}}>Super pouvoir</SvgText>
        </div>
        <img src={x < 0 ? LeftTop : RightTop} alt="" style={{width: '100%'}}/>
    </div>
};
const BubbleContainer = ({x,padding=10,children}) => {
    return <div style={{width:"100%",}}>
        <div style={{
            minHeight:"6em",
            // backgroundColor:"#ff00ff",
            //width:`calc( 100% - ${padding*2}px)`,padding:padding,
            width:`calc( 100% - ${0}px)`,padding:padding,
            position:'relative',
            backgroundImage:`${x<0 ?bgs.LeftMid:bgs.RightMid}`,
            backgroundSize: '100%',
            // backgroundSize: '125px 175px'

        }} className="bubblex-container">
            {children}
        </div>
    </div>
};
const BubbleFooter = ({x,selectedTab,validity,pack,total}) => {
    return <div  style={{width:'109%',marginLeft:x < 0 ?'0%':'-9%',transform:`translateY(${-8}px)`,pointerEvents: 'none',}}>
        <div style={{
            marginLeft:x < 0 ?'6%':'0%',
            position:'absolute', ...(x>=0?{right:0}:{}),
            width: '85%', height: '90%', display: 'flex',marginBottom:'10%',
            justifyContent: 'center',
            alignItems: 'center',
            // marginTop: '40%'
        }}>
            {selectedTab===0 && <SvgText style={{fill: '#6300d5', transform: 'scale(0.8) translateY(-4%)'}}
                     textStyle={{fontFamily: 'BebasNeue',}}>{validity}</SvgText>}
            {selectedTab===1 && <>
                <div style={{
                    width: '50%', display: 'flex',
                    justifyContent: 'center',
                    alignItems: 'center',
                }}>
                    <SvgText style={{fill: '#6300d5', transform: 'scale(1.2) '}}
                             textStyle={{fontFamily: 'BebasNeue',}}>{pack.validity}</SvgText>
                </div>
                <div style={{
                    width: '50%',display: 'flex',
                    justifyContent: 'center',
                    alignItems: 'center',
                }}>
                    <SvgText style={{fill: '#00ff7e', transform: 'scale(2) translateX(-2%)'}}
                             textStyle={{fontFamily: 'BebasNeue', textShadow: '1px 1px black'}}>{total} DA</SvgText>
                </div>
            </>}
        </div>
        <img src={x < 0 ? LeftBot : RightBot} alt="" style={{width: '100%'}}/>
    </div>
};
export default BubbleX;
