import React, {useEffect, useRef, useState} from "react";
import MapGenerator from "./MapGenerator";

import Facebook from "../../../Assets/map/social/facebook.svg"
import Messenger from "../../../Assets/map/social/messenger.svg"
import Instagram from "../../../Assets/map/social/instagram.svg"
import Whatsapp from "../../../Assets/map/social/whatsapp.svg"
import Youtube from "../../../Assets/map/social/youtube.svg"
import Snapchat from "../../../Assets/map/social/snapchat.svg"
import Midnight from "../../../Assets/map/social/Midnight.svg"
import MobileData from "../../../Assets/map/social/data.svg"
import Phone from "../../../Assets/map/social/minutes.svg"

import mpButton from "../../../Assets/map/button.png"
import BubbleRight from "../../../Assets/map/speech_pop_up_right_2.svg"
import BubbleLeft from "../../../Assets/map/speech_pop_up_left_2.svg"
import Roogy from "../../../Assets/map/roogy_map.png"
import PriceBubble from "../../../Assets/map/Union.png"
import './Game.css';
import {offerData} from "./offerData";
import {offerDataPC} from "./offerData";
import {useOutsideAlerter} from "../../useOutsideAlerter";
import useWindowDimensions from "../../useWindowDimensions";
import { Textfit } from 'react-textfit';
import BubbleX from "../../Bubble/BubbleX";
import {useHover} from "../../useHover";

function Game({mobile, container, selectedTab}) {
    const enableScroll = false;
    const [selectedOffer, set_selectedOffer] = useState(undefined);
    const [selectedRef, set_selectedRef] = useState(null);
    const selectOffer = (item, ref) => {
        set_selectedOffer(item);
        // console.log(item)
        if (item) {
            set_selectedRef(ref)
        }
    };
    const {height, width} = useWindowDimensions();
    useEffect(() => {
        if (container && selectedRef && selectedOffer && enableScroll) {
            // setTimeout(()=>{
            const button = selectedRef.current.getBoundingClientRect();
            const cont = container.current.getBoundingClientRect();
            const diff = () => {
                // if(button.top>(cont.height/2)){
                //     return button.top - (cont.height*0.9)
                // }else{
                return button.top - (cont.height * 1);
                // }
            };
            const minHeight = cont.height * 0.3
            console.log(`container height: ${Math.ceil(cont.height)},top: ${Math.ceil(button.top)}, diff: ${Math.ceil(diff())}`, button);
            // console.log();

            container.current.scrollTo({
                top: diff(),//<=-450?0:diff,//>-minHeight?diff+(minHeight/2):diff,
                behavior: 'smooth'
            });
            // },500);

        }
    }, [container, selectedOffer]);
    return <>
        {selectedTab === 0 && (mobile?offerData:offerDataPC).mois.bundle.map((item, index) => <MapButton key={index}
                                                                                    zIndex={offerData.mois.bundle.length - index}
                                                                                    selectedOffer={selectedOffer}
                                                                                    item={item}
                                                                                    selectOffer={selectOffer}
                                                                                    validity={offerData.mois.validity}
                                                                                    pack={offerData.mois.pack}/>)}
        {selectedTab === 1 && (mobile?offerData:offerDataPC).semaine.bundle.map((item, index) => <MapButton key={index}
                                                                                       zIndex={offerData.semaine.bundle.length - index}
                                                                                       selectedOffer={selectedOffer}
                                                                                       item={item}
                                                                                       selectOffer={selectOffer}
                                                                                       validity={offerData.semaine.validity}
                                                                                       pack={offerData.semaine.pack}/>)}
        {selectedTab === 2 && (mobile?offerData:offerDataPC).jour.bundle.map((item, index) => <MapButton key={index}
                                                                                    zIndex={offerData.jour.bundle.length - index}
                                                                                    selectedOffer={selectedOffer}
                                                                                    item={item}
                                                                                    selectOffer={selectOffer}
                                                                                    validity={offerData.jour.validity}
                                                                                    pack={offerData.jour.pack}/>)}

        <MapGenerator map={selectedTab} mobile={mobile}/>
    </>
}

export const Tabs = ({set_selectedTab, selectedTab,mobile}) => {
    return <div className={`offer-tabs ${mobile?'mobile':''}`}>
        <div onClick={() => set_selectedTab(0)} className={selectedTab === 0 ? 'selected' : ''}><SvgText>1 MOIS</SvgText>
        </div>
        <div onClick={() => set_selectedTab(1)} className={selectedTab === 1 ? 'selected' : ''}>
            <SvgText>15 JOURS</SvgText></div>
        <div onClick={() => set_selectedTab(2)} className={selectedTab === 2 ? 'selected' : ''}><SvgText>1 JOUR</SvgText>
        </div>
    </div>
};
export const SvgText = ({children, style, textStyle, onTextClick,viewBox="0 0 100 100"}) => {
    return <svg viewBox={viewBox} width="100%" style={style}>
        <text x="50%" y="50%" dominantBaseline="middle" textAnchor="middle" style={textStyle}
              onClick={onTextClick}>{children}</text>
    </svg>
};
const MapButton = ({
                       item = {
                           x: 0,
                           y: 0,
                           xb: 0,
                           yb: 0
                       }, pack = {}, validity = '', selectOffer, set_selectedRef, selectedOffer, zIndex
                   }) => {
    const wrapperRef = useRef(null);
    const [isSelected, set_isSelected] = useState(false);
    useOutsideAlerter(wrapperRef, (e, t) => {
        // console.log(checkSelected());
        if (isSelected) {
            // console.log(e,t.key);
            selectOffer(undefined, undefined);
        }
    }, [isSelected]);
    //===============[Hover effect start]=========================================
    const hovered=useHover(wrapperRef);
    const [isInit, set_isInit] = useState(false);
    useEffect(() => {
        if(isInit && !checkSelected()){
            // console.log("hovered",hovered);
            if(hovered){
                // selectOffer(item, wrapperRef);
            }else{
                // selectOffer(undefined, undefined);
            }
        }
    }, [hovered]);
    useEffect(() => {set_isInit(true)}, []);
    //===============[Hover effect end]=========================================
    const checkSelected = () => {
        if (selectedOffer && item) {
            return `${item.x}${item.y}${item.xb}${item.yb}${item.bundle}` === `${selectedOffer.x}${selectedOffer.y}${selectedOffer.xb}${selectedOffer.yb}${selectedOffer.bundle}`
        } else {
            return false
        }
    };

    useEffect(() => {
        set_isSelected(checkSelected())

    }, [selectOffer]);
    const buttonClick = () => {
        selectOffer(item, wrapperRef);
    };

    return <div style={{
        position: 'absolute',
        width: '20%',
        transform: `translate(${item.x}%, ${item.y})`,
        zIndex: zIndex,
        cursor: 'pointer'
    }}
                onClick={buttonClick} className="map-btn-container" ref={wrapperRef}>
        <img src={mpButton} alt="" width="100%" style={{verticalAlign: 'middle', pointerEvents: 'none',}}
             className="map-btn"/>
        <img src={PriceBubble} alt="" width="100%"
             style={{transform: `translate(0, -10%)`, verticalAlign: 'middle', pointerEvents: 'none'}}/>
        <div style={{
            position: 'absolute',
            width: '100%',
            textTransform: 'uppercase',
            bottom: '-24%',
            pointerEvents: 'none'
        }}><SvgText
            style={{fill: 'black', transform: 'scale(1.4) translateY(-2%)'}}
            textStyle={{fontFamily: 'BebasNeue'}}>{item.bundle} DA</SvgText></div>
        {/*{ isSelected && <>*/}
                <BubbleX item={item} x={item.xb} y={item.yb} validity={validity} pack={pack} show={isSelected||hovered}/>
                <RoogyMap show={isSelected}/>
            {/*</>}*/}
    </div>
};
const RoogyMap = ({show=false}) => {
    return <div style={{position: 'absolute', width: '100%', bottom: '70%', pointerEvents: 'none', display:show?'block':'none'}}>
        <img src={Roogy} alt="" style={{width: '100%', verticalAlign: 'middle'}}/>
    </div>
};
export default Game;
