import React, {useEffect} from "react";
import Road1 from '../../../Assets/map/map01.jpg'
import Road1_pc from '../../../Assets/map/map01_pc.jpg'
import Road2 from '../../../Assets/map/map02.jpg'
import Road2_pc from '../../../Assets/map/map02_pc.jpg'
import Road3 from '../../../Assets/map/map03.jpg'
import Road3_pc from '../../../Assets/map/map03_pc.jpg'
//import './MapGenerator.scss';
const style={width:'100%',verticalAlign: 'bottom',objectPosition: '0 -90vh',transform:'scale(1.5)'};

function MapGenerator({map,mobile}) {
    switch (map) {

        case 0:return <div style={{width:'100%'}}>
            <img src={mobile?Road1:Road1_pc} alt=""  style={{...style,objectPosition:'bottom'}}/>
        </div>;
        case 1: return <div style={{width:'100%'}}>
            <img src={mobile?Road3:Road3_pc}  alt="" style={{...style,objectPosition: 'bottom'}}/>
        </div>;
        default:
        case 2:return <div style={{width:'100%'}}>
            <img src={mobile?Road2:Road2_pc}  alt=""  style={{...style,objectPosition: mobile?'bottom':'0 8vh'}}/>
        </div>;
    }

}

export default MapGenerator;
