import React, {useState} from "react";
import ScrollGameHeading from "./ScrollGameHeading";
import ArrowButton from "../ArrowButton";
import Phone from "./Game/Phone";
import {Tabs} from "./Game/Game";
//import './ScrollGame.scss';

function ScrollGame({scrollToHeading,scrollToFooter,mobile}) {
    const [selectedTab, set_selectedTab] = useState(0);
    return  <section className={`yooz-offers-scroll-game ${mobile?'mobile':''}`} style={{position:'relative'}}>
        {/*<ArrowButton onClick={scrollToHeading} rotate={false} purple={!mobile} style={{*/}
        {/*    position:"absolute",*/}
        {/*    margin:'20px',*/}
        {/*    right:0,zIndex:5,*/}
        {/*    top:40*/}
        {/*}}/>*/}
        {/*<ArrowButton onClick={scrollToFooter} purple={!mobile}  style={{*/}
        {/*    position:"absolute",*/}
        {/*    margin:'20px',*/}
        {/*    right:0,zIndex:5,*/}
        {/*    bottom:60*/}
        {/*}}/>*/}
        {mobile && <Tabs set_selectedTab={set_selectedTab} selectedTab={selectedTab} mobile={mobile}/>}
        {!mobile && <ScrollGameHeading/>}
        <Phone mobile={mobile} set_selectedTab={set_selectedTab} selectedTab={selectedTab}/>
    </section>
}

export default ScrollGame;
