import React, {useState} from "react";
import Facebook from "../../Assets/map/social/facebook.svg";
import Messenger from "../../Assets/map/social/messenger.svg";
import Instagram from "../../Assets/map/social/instagram.svg";
import Whatsapp from "../../Assets/map/social/whatsapp.svg";
import Youtube from "../../Assets/map/social/youtube.svg";
import Snapchat from "../../Assets/map/social/snapchat.svg";
import Midnight from "../../Assets/map/social/Midnight.svg";
import PubgSuper1 from "../../Assets/map/social/pubg-super1.svg";
import PubgSuper2 from "../../Assets/map/social/pubg-super2.svg";
import TwoGo_Bonus from "../../Assets/map/social/2Go_Bonus.png";
import TikTok_4go from "../../Assets/map/social/tiktok_4go.svg";
import TikTok_2go from "../../Assets/map/social/tiktok_2go.svg";
import TikTok_1go from "../../Assets/map/social/tiktok_1go.svg";
import Phone50 from "../../Assets/map/social/phone-50min.svg";
import Sms from "../../Assets/map/social/smsunli.svg";
import {SvgText} from "../ScrollGame/Game/Game";
import IconText from "./IconText";

//import './SuperPower.scss';

const TiktokIconValidity={
    "Valable 1 Jour":TikTok_1go,
    "Valable 15 Jours":TikTok_2go,
    "Valable 30 Jours":TikTok_4go,
}
function SuperPower({pack,set_total,item,total,validity}) {
    return <div style={{
        height:'100%',
        width:'100%',
        display:'flex',
        flexWrap: 'wrap',
        alignItems:'center',
        justifyContent:'center',
    }}>
        {pack.facebook && pack.condition.facebook(item.bundle) && <IconText icon={Facebook} offer={pack.facebook} text={pack.facebook?`+${pack.facebook} DA`:undefined} setAmount={set_total} amount={total}/>}
        <IconText icon={Messenger} offer={pack.messenger} text={pack.messenger?`+${pack.messenger} DA`:undefined}  setAmount={set_total} amount={total}/>
        <IconText icon={Instagram} offer={pack.instagram} text={pack.instagram?`+${pack.instagram} DA`:undefined}  setAmount={set_total} amount={total}/>
        <IconText icon={Whatsapp} offer={pack.whatsapp} text={pack.whatsapp?`+${pack.whatsapp} DA`:undefined}  setAmount={set_total} amount={total}/>
        <IconText icon={Youtube} offer={pack.youtube} text={pack.youtube?`+${pack.youtube} DA`:undefined}  setAmount={set_total} amount={total}/>
        {
            pack.snapchat ?
            <IconText icon={Snapchat} offer={pack.snapchat} text={pack.snapchat?`+${pack.snapchat} DA`:undefined}  setAmount={set_total} amount={total}/>
            : null
        }
        <IconText icon={Phone50} offer={pack.offresMinuit} text={pack.offresMinuit?`+${pack.offresMinuit} DA`:undefined}  setAmount={set_total} amount={total}/>
         {
            pack.bonus2Gb ?
            <IconText icon={TwoGo_Bonus} offer={pack.bonus2Gb} text={pack.bonus2Gb?`+${pack.bonus2Gb} DA`:undefined}  setAmount={set_total} amount={total}/>
            //<Extra amount={pack.bonus2Gb} label={'2 Gb PUBG MOBILE + Skin'} />
            : null
        }
        {
           pack.sms ?
           <IconText icon={Sms} offer={pack.sms} text={pack.sms?`+${pack.sms} DA`:undefined}  setAmount={set_total} amount={total}/>
           // <Extra amount={pack.sms} label={'SMS illimités vers tous les réseaux'} />
           : null
       }
        {
            pack.tiktok ?
                <IconText icon={TiktokIconValidity[validity]} offer={pack.tiktok} text={pack.tiktok?`+${pack.tiktok} DA`:undefined}  setAmount={set_total} amount={total} scale={1.7}/>
                //<Extra amount={pack.bonus2Gb} label={'2 Gb PUBG MOBILE + Skin'} />
                : null
        }
    </div>
}

const Extra = ({ label, amount }) => {
    return (
        <>
            <SvgText
                style={{fill: '#000000', height: 20, transform: 'scale(4)',pointerEvents: 'none', marginBottom: -10}}
                textStyle={{fontFamily: 'BebasNeueRegular',pointerEvents: 'none', }}>{label}</SvgText>
            <SvgText
                style={{fill: '#000000', height: 40, transform: 'scale(4)',pointerEvents: 'none',}}
                textStyle={{fontFamily: 'BebasNeue',pointerEvents: 'none', }}>+{amount} DA</SvgText>
        </>
    )
}

export default SuperPower;
